webpackJsonp([14,56],{

/***/ 840:
/***/ (function(module, exports) {

	/*markdown-loader*/
	module.exports = {
	  "content": "<h2 id=微交互是什么？><a href=#%E5%BE%AE%E4%BA%A4%E4%BA%92%E6%98%AF%E4%BB%80%E4%B9%88%EF%BC%9F aria-hidden=true><span class=\"icon icon-link\"></span></a>微交互是什么？</h2><p>所谓的微交互顾名思义就是更关注交互细节，他决定了用户体验是友好的还是令人勉强能用的？交互设计师不仅要把握产品的大方向，而且更要关注产品的细节体验。因为细节交互是每时每刻的体验，如果大方向把握的很好，但是细节做不到位那这个产品不能算一个合格的、体验优良的产品。</p><h2 id=生活中的微交互><a href=#%E7%94%9F%E6%B4%BB%E4%B8%AD%E7%9A%84%E5%BE%AE%E4%BA%A4%E4%BA%92 aria-hidden=true><span class=\"icon icon-link\"></span></a>生活中的微交互</h2><p>做互联网产品，我们不仅要仅仅关注互联网，更需要关注生活。生活中的交互常常会让我们学到更多的东西，并应用到互联网产品设计中。微交互不仅仅指互联网的产品，在我们生活当中也时时刻刻都存在着微交互。</p><p><strong>例1：私家车</strong></p><img src=http://ww4.sinaimg.cn/large/699ef9c0jw1evxkvui62dj20o10d6my3.jpg alt width=865 height=474><p><strong>图1</strong></p><p>私家车的宏交互是出行的工具，可以带我去很多地方就可以了。</p><p>微交互：如果有良好的视觉感受，很好的体验，比如握住把手，把手的灯和车内的灯就会亮起、门开了后，人坐进去，座椅就可以调节到合适的位置，后视镜调到合适的角度。甚至根据开车人的喜好调到喜欢的音乐。还要自动泊车系统等等。</p><p>这些微交互我们明明可以自己手动完成，这些也都是小事，但是当汽车帮我们完成了这些『小事』之后，我们坐到车里面就有一种情不自禁的喜悦。难道这不是每一个汽车制造商都希望自己的客户能体验到的感受吗？我们平时做设计也是这样。</p><p><strong>例2：冰箱</strong></p><img src=http://ww1.sinaimg.cn/large/699ef9c0gw1evxl3shhbhj20kn08kac2.jpg alt width=743 height=308><p><strong>图2</strong></p><p>正常的冰箱，没有什么特别的。就是保存食物的功能，可以使我们的食物延长保质时间。但是直到有一天我家冰箱的灯坏了，这时候我找任何东西都不是那么方便。我才发现这样的细节体验是那么的重要。</p><p><strong>例3：牙膏</strong></p><img src=http://ww4.sinaimg.cn/large/699ef9c0gw1evxl61cegsj20jz0900tu.jpg alt width=719 height=324><p><strong>图3</strong></p><p>这两个牙膏是我们平时经常使用的两款牙膏，大家觉得这两款牙膏哪个更方便快捷好用？大家可能会选中右边这个。之前也看过一个研究，左面旋钮式拧开瓶盖时间大概是3s，右面按扣式的1s。尤其再早晨这样宝贵的时间，拧开拧上6s钟时间，没准我们就可以赶上地铁了，是吧？当然早晨刷牙的时候可能还睡眼悻悻，可能我们不能很准确的找到按扣式的开口方向，那我们怎么解决呢？</p><img src=http://ww1.sinaimg.cn/large/699ef9c0gw1evxl7pr900j20m20dh0tx.jpg alt width=794 height=485><p><strong>图4</strong></p><p>后来我遇到了这款牙膏，他是卡口式，时间成本上其实和按扣式差不多，但是却能给我带来惊喜，这就是创新的效果。这样的微创新适合再互联网产品上，因为用户每时每刻都希望能有新奇的事物发生。</p><p><strong>微交互不容小视—微交互可以是产品的组成部分，也可以是整个产品。</strong></p><p>可能你还会觉得微交互也就是整个产品的细枝末节，没什么重要的。那我就给你举几个微交互是整个产品的例子。</p><p><strong>例1：煮蛋器</strong></p><img src=http://ww2.sinaimg.cn/large/699ef9c0gw1evxlcel6s6j20dq0dumyf.jpg alt width=494 height=498><p><strong>图5</strong></p><p>为什么说微交互不容小视，因为有时候一个微交互也可以是一个产品。像煮蛋器就只做一件事，我们把鸡蛋洗好，放到鸡蛋槽里面，盖上盖子，按下开关就可以了。</p><p><strong>例2：计算器</strong></p><img src=http://ww2.sinaimg.cn/large/699ef9c0gw1evxlcsam46j20iy0dmt96.jpg alt width=682 height=490><p><strong>图6</strong></p><p>微交互的小应用也不胜枚举，例如这个客户端上的计算器、图片查看器等等。</p><h3 id=微交互要考虑使用场景><a href=#%E5%BE%AE%E4%BA%A4%E4%BA%92%E8%A6%81%E8%80%83%E8%99%91%E4%BD%BF%E7%94%A8%E5%9C%BA%E6%99%AF aria-hidden=true><span class=\"icon icon-link\"></span></a>微交互要考虑使用场景</h3><p><strong>例1：手机闹钟</strong></p><p>手机关机的时候，闹钟是否要按时响？飞行模式的时候呢？手机静音的时候？</p><img src=http://ww2.sinaimg.cn/large/699ef9c0gw1evxldava5vj20lj0dujru.jpg alt width=775 height=498><p><strong>图7</strong></p><p>这是我一个同学，她之前用三星，新买的苹果手机，她的使用习惯是，每天睡觉之前关闭手机，由于不了解苹果手机的习性，第二天闹钟没有响，导致她第二天上班迟到…大家可以讨论一下，在手机关机的时候或者飞行模式的时候是否要把所有声音全部关闭？————（现在苹果手机是关机的时候任何声音都会关闭，飞行模式仅仅是阻断信号，闹钟可以正常使用。）</p><p><strong>例2：电脑静音</strong></p><p>电脑静音的情况下，我在设置中点击试听，是应该播放声音还是不播放声音？</p><img src=http://ww3.sinaimg.cn/large/699ef9c0gw1evxldqm4k2j20dq06i0tx.jpg alt width=494 height=234> <img src=http://ww1.sinaimg.cn/large/699ef9c0gw1evxleh8rg6j20gu05cglu.jpg alt width=606 height=192><p><strong>图8</strong></p><p>这儿ios系统的不一致性：大家都知道，苹果手机静音时候是可以听到闹铃的，而电脑静音，我打开设置，点击播放声音却不行？像这两张图，我都从很深的入口把设置调出来，然后打开的这个设置，再点击声音却没有任何反应，而且也没有任何的提示。</p><p>从使用场景考虑：静音应该仅仅是禁止非用户明确要求或者非专门让发出的声音（如短信提示音、来电铃声）。而不应该禁止用户特别要求发出的声音。</p><h2 id=微交互设计构成><a href=#%E5%BE%AE%E4%BA%A4%E4%BA%92%E8%AE%BE%E8%AE%A1%E6%9E%84%E6%88%90 aria-hidden=true><span class=\"icon icon-link\"></span></a>微交互设计构成</h2><img src=http://ww4.sinaimg.cn/large/699ef9c0gw1evxlevfk12j20o003sdg7.jpg alt width=864 height=136><p><strong>图9</strong></p><p>触发器：触发器启动微交互</p><p>规则：规则规定微交互的工作流程</p><p>反馈：反馈向用户说明规则</p><p>模式和循环：循环与模式是影响微交互的元规则</p><h3 id=触发器><a href=#%E8%A7%A6%E5%8F%91%E5%99%A8 aria-hidden=true><span class=\"icon icon-link\"></span></a>触发器</h3><p>任何可以能够启动微交互的东西。</p><p>手动触发器——由用户发起的。可以是控件、图标、表单、触摸或手势</p><p>系统触发器——会在某个或某些条件具备时被触发</p><p>不可见触发器——手势、头像、指纹识别等（移动设备上）；命令键或移动到屏幕一角。</p><img src=http://ww4.sinaimg.cn/large/699ef9c0gw1evxlfcjz8vj207i03h3yb.jpg alt width=270 height=125> <img src=http://ww4.sinaimg.cn/large/699ef9c0gw1evxlfpqzogj209606wwen.jpg alt width=330 height=248><p><strong>图10</strong></p><img src=http://ww4.sinaimg.cn/large/699ef9c0gw1evxlgijd7cj20a3024dfs.jpg alt width=363 height=76> <img src=http://ww2.sinaimg.cn/large/699ef9c0gw1evxlgtky29j20a30230sv.jpg alt width=363 height=75><p><strong>图11</strong></p><p>手动触发的非常多，例如图10：表情、发送图片、截图、传文件。设置、历史都是一个微交互的手动触发器。系统触发器最常见的如图11：收到消息收到邮件等。不可见的触发器如手机上从底部向上滑动可以调出各种快捷操作和设置。屏幕上方向下滑动会拉出消息通知。Windows系统鼠标滑倒右下角会最小化桌面打开的所有程序。</p><h3 id=规则><a href=#%E8%A7%84%E5%88%99 aria-hidden=true><span class=\"icon icon-link\"></span></a>规则</h3><p>微交互的规则：决定了微交互怎么用。</p><p>创建规则的核心：围绕微交互如何运作的简单的非技术性心智模型</p><p>即：动机+操作+目的</p><img src=http://ww4.sinaimg.cn/large/699ef9c0gw1evxlhadtanj20900axmxc.jpg alt width=324 height=393><p><strong>图12</strong></p><p>确定规则之前要明确这个微交互的目的是什么？了解用户的使用动机和目标，微交互越接近目标，成功的概率就越高。举一个例子，如图12：登录界面。登录的微交互目标不是让用户输入帐号和密码，而是让用户完成登录并进入应用程序。那么我们在设计这个界面时候就需要考虑尽量减少用户的操作成本，让用户快速的完成登录。在用户输入帐号的时候回关联记忆上次输入的内容，如果已经存储了密码，会自动关联出密码等等这样的微交互设计。</p><h3 id=反馈><a href=#%E5%8F%8D%E9%A6%88 aria-hidden=true><span class=\"icon icon-link\"></span></a>反馈</h3><p>反馈：帮助用户了解微交互的规则</p><p>微交互反馈的最重要的原则：别让反馈给用户造成压力</p><p>1.如何判断？——怎么用最少的反馈传达同样的信息？</p><img src=http://ww4.sinaimg.cn/large/699ef9c0gw1evxlhnsyicj20fa0350ta.jpg alt width=550 height=113><p><strong>图13</strong></p><p>反馈应该由需求驱动：用户需要知道什么？什么时候知道？（多久一次？）就如前面我们说的那个例子，图13：邮件提醒。邮件客户端检查新邮件的时候，不是每次检查都会告知用户，而是当检查有新邮件的时候才会通知用户。</p><p>2.如何让反馈有趣</p><table><tbody><tr><td><img class=aligncenter src=http://ww3.sinaimg.cn/large/699ef9c0gw1evxlisla4cj206y09pglu.jpg alt width=250 height=349></td><td><img class=aligncenter src=http://ww2.sinaimg.cn/large/699ef9c0gw1evxlj73fk5j205z09jmx2.jpg alt width=215 height=343></td><td><img class=aligncenter src=http://ww3.sinaimg.cn/large/699ef9c0gw1evxlkce6ztj206309oglj.jpg alt width=219 height=348></td></tr></tbody></table>**图14**<p>一个简单的例子，如图14：各种空页面、加载页面、加载失败页面、添加成功界面等，都是用一种很可爱的或静态或动态的图给用户一种友好的反馈。</p><p>3.反馈机制</p><ul><li>A环境变化</li><li>B持续时间</li><li>C强烈程度</li></ul><img src=http://ww1.sinaimg.cn/large/699ef9c0gw1evxlkq7hjij20jz0jltar.jpg alt width=719 height=705><p><strong>图15</strong></p><p>环境变化的反馈：如阅读类的应用会根据光线的不同而调节界面的亮度。</p><p>持续时间：类似这样的toast提示是轻量的，会出现在界面的中间位置，所以文字字数要有一定的限制，时间也要有一定的限制，一般1-3s。时间过长会影响用户正常使用产品，影响产品的体验。</p><p>强烈程度：像用户主动触发的并且会有一定的后果的会用比较强烈的提示方式，如果不会造成很严重的后果，可以通知到用户即可。</p><p>重复次数：以上的反馈均是每次都会出现的.</p><h3 id=模式和循环><a href=#%E6%A8%A1%E5%BC%8F%E5%92%8C%E5%BE%AA%E7%8E%AF aria-hidden=true><span class=\"icon icon-link\"></span></a>模式和循环</h3><p><strong>模式：是规则的一个分支，常见的模式如编辑模式和设置模式。</strong></p><p>A.如果用户需要在不同模式之间切换，那就最好可以切换不同的屏幕，否则可能用户很容易迷茫。</p><p>B.设置模式本身目的就是执行一种不常用的交互，可能会妨碍达到主要的目的。</p><img src=http://ww2.sinaimg.cn/large/699ef9c0gw1evxma6oxdnj20hq057dgb.jpg alt width=638 height=187><p><strong>图16</strong></p><p><strong>循环：确定微交互的速度和持续时间</strong></p><img src=http://ww3.sinaimg.cn/large/699ef9c0gw1evxmanql7aj20e0071aan.jpg alt width=504 height=253><p><strong>图17</strong></p><p>如果需要在不同的屏幕之间切换，那最好使用不同的屏幕。如编辑资料的时候，点击编辑会让原有的界面直接变成一个新界面。第一张图就是关于提示新消息的设置。是最常见的妨碍微交互到达的主要目的。</p><p>在工作中我们要养成总结归纳规范的习惯。</p>",
	  "extra": {
	    "_image-loader_": []
	  }
	}

/***/ })

});


